$white: #fff;

.preloader {
  position: fixed;
  z-index: 13;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: $white;
  display: flex;
  justify-content: center;
  align-items: center;

  p {
    font-family: inherit;
    font-size: 20px;
    margin-left: 10px;
    color: $black;
  }
}